
import * as _ from '../../utils/tools'
import Toast from 'vux/src/components/toast/index.vue'

export default {
components: {
Toast
},
data: function () {
return {
src: '/verifycode/captcha?t=' + Math.random(),
username: '',
password: ''
}
},
methods: {
register: function () {
if (!this.username) {
this.$vux.toast.show({
text: '请输入用户名',
type: 'warn'
})
return false
}
if (!this.password) {
this.$vux.toast.show({
text: '请输入密码',
type: 'warn'
})
return false
}
let data = {
'username': this.username,
'password': this.password
}
return this.$api.register(data).then(res => {
this.$vux.toast.text('注册成功')
_.go('/login', this.$router, 2000)
}).catch(err => {
this.$vux.toast.text('用户名已存在')
return err.message
})
}
}
}



